
import { AppBar, IconButton, Typography, TextField, InputAdornment, Button } from "@material-ui/core";
import SearchIcon from '@material-ui/icons/Search';
import MenuIcon from '@material-ui/icons/Menu';



function NavBar({handleOpen}) {
   
    return (
        <AppBar position='static' color='transparent'>
            <div className='bg-gray-900 flex flex-col lg:flex-row  p-2 lg:items-center bg-opacity-90 text-white'>
                <div className='flex items-center'>
                <IconButton color='inherit'><MenuIcon /></IconButton>
                    <Typography variant='h6'>API Weather App</Typography>
                </div>
                <div className='bg-red-90  items-center sm:justify-center flex flex-col-reverse sm:flex-row flex-grow sm:space-x-2 space-y-2
                lg:space-y-0'>
                    <TextField margin='dense' size='small' type='search' placeholder='Enter the Name of a City'
                        InputProps={{
                            startAdornment: (
                              <InputAdornment position="start">
                                <SearchIcon />
                              </InputAdornment>
                            ),
                          }}
                        className='w-11/12 sm:w-7/12 lg:w-6/12 xl:w-5/12 bg-white text-black rounded-xl' />
                    <div className='flex space-x-2 '>
                        <Button size='small' variant="outlined" className='h-10 lg:self-center'>Search</Button>
                        <Button onClick={handleOpen} size='small' variant='outlined' color='error' className='h-10 lg:self-center'>Options</Button>
                       
                    </div>
                </div>

            </div>
        </AppBar>
    )
};


export default NavBar;